body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 600px;
  max-height: 600px;
  background: #444;
  color: #fff;
}

body .app {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

body .app .container {
  margin: 0 5rem;
}

body .app .container .city-infos .city-container table {
  margin-top: 1rem;
}

body .app .container .city-infos .city-container table, td, th {
  border: 1px solid #ddd;
  text-align: left;
}

body .app .container .city-infos .city-container table {
  border-collapse: collapse;
  width: 100%;
}

body .app .container .city-infos .city-container th, td {
  padding: 1%;
}

body .app .container .error {
  padding: 1%;
  margin-top: 1rem;
  border: 1px solid #ff0000;
  background: #ff5555;
  color: #fff;
}